import axios from 'axios';
import FormData from 'form-data';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';

export default {
    namespaced: true,
    state: () => ({
    }),
    getters: {
    },
    mutations: {
    },
    actions: {
        createInstruction: async (context, name) => {
            const formdata = new FormData();
            formdata.append("func", "ll");
            formdata.append("objType:", "5574");
            formdata.append("objAction", "create2");
            formdata.append("parentId", opentext.instructionId);
            formdata.append("MimeType", "text%2Fhtml");
            formdata.append("textfield", "");
            formdata.append("name", name);
            formdata.append("nextURL", "%2Fotcs%2Fllisapi.dll");

            const config = {
              method: 'post',
              url: `${opentext.mainurl}/otcs/llisapi.dll`,
              data : formdata
            };
            try{
                const response = await axios(config)
                console.log(response);
            }catch(error){
                console.log(error);
            };
        }
    }
}